import React, { Fragment } from 'react';
import bridge from '@vkontakte/vk-bridge';
import {
	View,
	Panel,
	Avatar,
	RichCell,
	Snackbar,
	PanelHeader,
	Button,
	Alert
} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import Icon28GameOutline from '@vkontakte/icons/dist/28/game_outline';
import Icon16Done from '@vkontakte/icons/dist/16/done';
import Recent from '@vkontakte/icons/dist/16/recent';

class App extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			activePanel: 'main',
			activeModal: null,
			fetchedUser: {},
			popout: null,
			snackbar: null
		}

		this.openErrorSnackbar = this.openErrorSnackbar.bind(this);
		this.openDoneSnackbar = this.openDoneSnackbar.bind(this);
	}

	componentDidMount() {
		bridge.send('VKWebAppGetUserInfo').then((user) => {
			this.setState({
				fetchedUser: user
			});
		});

		if(bridge.supports('VKWebShowAppNativeAds')) {
			bridge.send('VKWebAppShowNativeAds', { ad_format: 'preloader' });
		}
	}

	openErrorSnackbar = (text) => {
		this.setState({
			snackbar:
			<Snackbar
				duration={1000}
				layout="vertical"
				onClose={() => this.setState({ snackbar: null })}
				before={<Avatar size={24} style={{backgroundColor: 'orange'}}><Recent fill="#fff" width={14} height={14} /></Avatar>}
			>
				{text}
			</Snackbar>
		});
	}

	openDoneSnackbar = (text) => {
		this.setState({
			snackbar:
			<Snackbar
				duration={1000}
				layout="vertical"
				onclose={() => this.setState({ snackbar: null })}
				before={<Avatar size={24} style={{backgroundColor:'green'}}><Icon16Done fill="#fff" width={14} height={14} /></Avatar>}
			>
				{text}
			</Snackbar>
		});
	}

	render() {
		return (
			<View
				id='home'
				activePanel={this.state.activePanel}
				popout={this.state.popout}
			>
				<Panel id='main'>
					<PanelHeader>Ученик года</PanelHeader>
					<RichCell
						disabled
						before={<Avatar size={72} src={this.state.fetchedUser.photo_100}/>}
						text={
							<span>Самый лучший ученик!</span>
						}
						actions={
							<React.Fragment>
								<Button mode="tertiary" before={<Icon28GameOutline/>} onClick={() => {
									this.setState({
										popout: <Alert
											onClose={() => this.setState({ popout: null })}
											actions={[{
												title: 'Отмена',
												autoclose: true,
												mode: 'cancel'
											}, {
												title: 'ОК',
												autoclose: true,
												action: () => {
													bridge.send('VKWebAppShowNativeAds', { ad_format: 'reward' })
													.then((res) => {
														window.location = 'https://gdz.ru';
													})
													.catch((err) => {
														this.openErrorSnackbar(`Посмотрите рекламу до конца!`)
													});
												}
											}]}
										>
											<h2>Подтвердите действие</h2>
											<p>Сейчас Вам будет показана реклама, которую необходимо посмотреть полностью и Вы перейдёте на ГДЗ.</p>
										</Alert>
									});
								}}>Перейти на ГДЗ</Button><br/>
							</React.Fragment>
						}
					>
						{this.state.fetchedUser.first_name} {this.state.fetchedUser.last_name}
					</RichCell>
					{this.state.snackbar}
				</Panel>
			</View>
		);
	}
}

export default App;